html {
  font-size: 10px;
  width: 100%;
  overflow-x: hidden;
}

body {
  min-width: 320px;
  font-size: 1.6rem;
  line-height: 1.6;
  font-family: $df;
  overflow-x: hidden;
  opacity: 1;
  position: relative;
  background-color: $white;
  margin: 0;
}

iframe{
  margin-bottom: -1rem;
}

img{
  max-width: 100%;
}

h1{
  @include font($fz:4rem, $color:$accent-1);
  margin-top: 0;
  margin-bottom: 0.3em;
}
h2, p{
  margin-top: 0;
  margin-bottom: 2em;
}
p{
  &:last-child{
    margin-bottom: 0;
  }
}

h1 + h2{
  @include font($fz:1.8rem, $color:$accent-3-d);
  font-weight: 300;
}

h2{
  @include font($fz:2.4rem, $color:$accent-1);
  margin-top: 0;
}

section{
  h1,h2,h3{
    text-align: center;
  }
}

a {
  color: $accent-2;
  transition: color 200ms ease;
  &:hover, &:focus {
    color: $accent-1;
  }
}

strong{
  letter-spacing: 0.04em;
}

.dark_bg{
  @include dark_bg();
  h2,h3,p{
    color: inherit;
  }
}

.fill_bg{
  background-position: center;
  background-size: cover;
  @include padding($y:100px);
}

// Small devices (landscape phones, < than 768px)
@include media-breakpoint-down(sm) {
  h1 {
    font-size: 2.8rem;
  }

  h2{
    font-size: 1.6rem;
  }
}