@import "_mixins/mixins";
@import 'vars';
@import 'vendor/all';
@import 'modules/all';
@import 'partials/all';

.policy{
  position: relative;
  height: 0;
  margin-top: 0;
  overflow-y: auto;
  transition: height 300ms ease-in-out;
  .container{
    padding-top: 6rem;
    padding-bottom: 6rem;
  }
  &__open{
    height: 40rem;
    max-height: 100vh;
  }
  &__close{
    z-index: 1;
    &:hover, &:focus{
      text-decoration: none;
    }
  }
}

@import 'media'; // Всегда в конце
