.elements{
	&__list{
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		margin-left: -1.5rem;
		margin-right: -1.5rem;
	}
	&__link{
		display: block;
		width: 100%;
		height: 6rem;
		margin-bottom: 0.5rem;
		padding-bottom: 1rem;
		border-bottom: 2px solid transparent;
		transition: all 200ms ease-in-out;
		img{
			max-width: 100%;
			max-height: 100%;
			width: auto;
			height: auto;
		}
	}
	a.elements__link{
		cursor: pointer;
		&:hover, &:focus{
			border-bottom-color: $accent-3;
		}
	}
}