.socmedia {
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  li {
    display: inline-block;
    margin-right: 2rem;
    &:last-child {
      margin-right: 0;
    }
  }
}

// Small devices (landscape phones, < than 768px)
@include media-breakpoint-down(sm) {
  .socmedia{
    margin-bottom: 2rem;
  }
}