@import "slick/slick";

$slick-arrow-color: $white !default;
$slick-dot-color: $black !default;
$slick-dot-color-active: $accent-1 !default;
$slick-opacity-default: 1 !default;
$slick-opacity-on-hover: 1 !default;
$slick-opacity-not-active: 1 !default;

/* Arrows */

.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0;
  font-size: 0;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  transform: translate(0, -50%) rotate(45deg);
  border-width: 2px;
  border-style: solid;
  border-color: $white;
  padding: 0;
  outline: none;
  z-index: 2;
  &:hover, &:focus {
    outline: none;
    background: transparent;
    color: transparent;
    &:before {
      opacity: $slick-opacity-on-hover;
    }
  }
  &.slick-disabled:before {
    opacity: $slick-opacity-not-active;
  }
  &:before {
    font-size: 20px;
    line-height: 1;
    color: $slick-arrow-color;
    opacity: $slick-opacity-default;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
}

.slick-prev {
  left: 25px;
  border-top: transparent;
  border-right: transparent;
}

.slick-next {
  right: 25px;
  border-bottom: transparent;
  border-left: transparent;
}

/* Dots */

.slick-dots {
  position: absolute;
  right: 1.5%;
  bottom: 4rem;
  list-style: none;
  display: inline-flex;
  justify-content: center;
  padding: 0;
  margin: 0;
  z-index: 2;
  li {
    position: relative;
    display: inline-block;
    height: 20px;
    width: 20px;
    margin: 0;
    padding: 5px;
    cursor: pointer;
    button {
      border: 1px solid $slick-dot-color;
      display: block;
      height: 8px;
      border-radius: 50%;
      width: 8px;
      outline: none;
      line-height: 0;
      font-size: 0;
      background-color: transparent;
      padding: 4px;
      box-shadow: 0 0 0 $accent-2;
      cursor: pointer;
      transition: all 200ms ease-in-out;
      &:hover, &:focus {
        outline: none;
        background-color: $accent-2;
        box-shadow: 0 0 5px $accent-2;
      }
    }
    &.slick-active button {
      background-color: $slick-dot-color-active;
      opacity: $slick-opacity-default;
    }
  }
}