.footer {
  position: relative;
  padding: 1rem 0;
  letter-spacing: 0.02em;
  color: $black;
  font-size: 1.4rem;
  font-weight: 300;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  z-index: 1;

  &__right {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    font-size: 12px;
  }

  &__pages {
    padding: 0;
    margin: 0;
    margin-left: 15px;
    font-size: inherit;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    
    li {
      list-style: none;

      a{
        color: inherit;
      }

      &:not(:last-child) {
        margin-right: 5px;
      }
    }
  }
  .copywrite {
    font-size: inherit;
  }

  &__center {
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@supports (display: grid) {
  .footer {
    display: grid;
    grid-template-columns: 7fr 30px 7fr;
  }
}
// Small devices (landscape phones, < than 768px)
@include media-breakpoint-down(sm) {
  .footer {
    .copywrite {
      text-align: center;
    }
  }
}

