*:focus {
  outline: none;
}

*::-webkit-input-placeholder {
  color: $cL-2;
  opacity: 0.6;
}

*:-moz-placeholder {
  color: $cL-2;
  opacity: 0.6;
}

*::-moz-placeholder {
  color: $cL-2;
  opacity: 0.6;
}

*:-ms-input-placeholder {
  color: $cL-2;
  opacity: 0.6;
}

input:focus:required:invalid,
textarea:focus:required:invalid {
  color: $cO-2;
}

input:required:valid,
textarea:required:valid {
  color: $cO-2;
}