.header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 999;
  padding: 0;
  font-size: 1.6rem;
  background: $white;
  transform: translateY(0);
  animation: hideNav 200ms ease-in-out forwards;
  transition: background 100ms ease-in-out;
  &__info{
    &>*:not(:last-child){
      margin-right: 1rem;
    }
  }
  &__top {
    background-color: $light;
    font-size: 1.4rem;
    padding: 0.5rem 0;
    .container {
      display: flex;
      flex-direction: row-reverse;
    }
    svg{
      width: 1.6rem;
      margin-right: .5rem;
      top: .3rem;
      position: relative;
      *{
        fill: $accent-2;
      }
    }
    a {
      font-size: 1.4rem;
      color: $accent-3-d;
      text-decoration: none;
      position: relative;
      display: inline-block;
      transition: color 200ms ease;
      &:hover, &:focus{
        color: $accent-1;
      }
    }
    &--link {
      text-decoration: none;
      position: relative;
      display: inline-block;
      transition: color 200ms ease;

    }
  }
  &__nav {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 1rem 0;
    &--logo {
      height: 7rem;
      font-size: 2.5rem;
      display: inline-flex;
      align-items: center;
      justify-content: flex-start;
      justify-self: flex-start;
      transition: all 200ms ease;
      
      span {
        font-weight: 700;
      }
      
      &:hover, &:focus {
        opacity: 0.8;
        text-decoration: none;
      }
    }
    &--navigate {
      text-align: right;
    }
  }
}

.header--scrol-hidde {
  position: fixed;
  top: 0;
  transform: translateY(0%);
  border-bottom: 1px solid rgba($light, 0.5);
  animation: showNav 300ms ease-in-out forwards;
  & .header__nav {
    &--logo {
      // height: 5rem;
    }
  }
}

@keyframes showNav{
  from{
    transform: translateY(-100%);
  }
  to{
    transform: translateY(0%);
  }
}
@keyframes hideNav{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

// Large devices (desktops, < than 768px)
@include media-breakpoint-down(sm) {
  .header {
    &__nav {
      z-index: 2000;
      padding: 1rem 0;
      &--logo {
        // height: 5rem;
      }
    }
  }
}

// Extra small devices (portrait phones, < than 576px)
@include media-breakpoint-down(xs) {
  .header {
    &__info{
      width: 100%;
      display: flex;
      justify-content: space-between;
    }
  }
}
