.bg{
	&--narrow{
		@include section($maxw:700px);
	}
	&--no{
		@include section($maxw:1200px);
	}
	&--blue{
		@include padding($y:80px);
		background: $accent-3;
		background: radial-gradient(ellipse at center, $accent-3 0%, darken($accent-3,10%) 100%);
		h2, h3, h3 *{
			color: $white;
			a:hover, a:focus{
				color: $accent-1;
				text-decoration: none;
			}
		}
		p{
			color: $white;
			opacity: 0.6;
		}
	}
}