.news{
	margin-bottom: 6rem;
	&__footer{
		display: flex;
		justify-content: center;
		padding-top: 3rem;
	}
}

.new{
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
	padding-top: 2.5rem;
	padding-bottom: 2rem;
	&:not(:first-child){
		border-top: 1px solid rgba($gray,0.2);
	}
	&__img{
		height: 40rem;
		position: relative;
	    background-size: cover;
	    background-position: center;
	    margin-bottom: 2rem;
	}
	&__header{
		width: 100%;
	}
	&__date{
		font-size: 1.2rem;
		color: rgba($black,0.2);
		margin-bottom: 0;
	}
	&__title{
		font-size: 3rem;
		color: $accent-3;
		margin-bottom: 0.6rem;
		margin-top: 0;
		line-height: 1;
		&:hover, &:focus{
			text-decoration: none;
		}
	}
	&__content{
		font-size: 1.8rem;
		line-height: 3.2rem;
		margin-top: 0.2em;
		width: calc(100% - 180px);
	}
	&__btn{
		display: block;
	}
}

// Medium devices (tablets, < than 992px)
@include media-breakpoint-down(md) {
	.new{
		&__img{
			height: 45vw;
		}
		&__content{
			width: 100%;
		}
		&__btn{
			margin-top: 1rem;
		}
	}
}