.contact {
  &__form {
    width: 100%;
    @include container($w: 100%, $maxw: 500px, $mx: auto);
    margin-right: 0;
  }
}

#kontakt_form {
  overflow: hidden;
}

#kontakt_map {
  height: 0;
  transition: height 200ms ease-in-out;
  &.openMap{
    height: 40rem;
  }
}


// Small devices (landscape phones, < than 768px)
@include media-breakpoint-down(sm){
  #kontakt_map {
    &.openMap{
      height: 25rem;
    }
  }
}