$df: 'Roboto', arial, sans-serif;

@mixin font($fz, $ls:0,$color:$dark, $tt:none, $lh:1){
  font-size: $fz;
  letter-spacing: $ls;
  color: $color;
  text-transform: $tt;
  line-height: $lh;
}

