#heroSlider{
  opacity: 0;
  transition: opacity 300ms ease-in-out;
  height: calc(100vh - 90px);
  figure, .slick-track, .slick-list{
    height: calc(100vh - 90px);
  }
}
.hero {
  position: relative;
  background-size: cover;
  background-position: center;
  &, figure, .slick-track, .slick-list{
    height: 55rem;
  }

  &__slide{
    background-size: cover;
    background-position: center;
    color: $black;

    &--invert {
      color: $white;

      .slick-dots {
        li {
          button {
            border: 1px solid $white;
            
            &:hover, &:focus {
              background-color: $white;
            }
          }
          &.slick-active button {
            background-color: $white;
          }
        }
      }
    }

  }
  
  figcaption{
    height: 100%;
    position: relative;
    padding: 6rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    color: inherit;
    h1{
      @include font($fz:8rem, $color:inherit);
      margin-bottom: 0.2em;
    }
    h2{
      @include font($fz:3rem,$color:inherit);
      margin: 0;
      font-weight: 300;
      margin-bottom: 0.6em;
    }
  }
}

// Medium devices (tablets, < than 992px)
@include media-breakpoint-down(md) {
  .hero {
    &, figure, .slick-track, .slick-list{
      height: 35rem;
    }

    figure {
      figcaption {
        h1 {
          font-size: 4rem;
        }
        h2{
          font-size: 1.8rem;
        }
      }
    }
  }
}